<template>
  <div class="build1">
    <back-home></back-home>
    <transition name="part1">
      <div class="part1" v-if="part1Show" @mousewheel.prevent="mouseWheel">
        <div class="banner" ref="banner1" :style="{ left: imgLeft + 'px' }">
          <img
            src="../../../assets/buildInfo/build3/天-04.png"
            @load="imgLoad"
          />
        </div>
        <div class="word" ref="word1" :style="{ top: wordTop + 'px' }">
          <p class="pFirst">旧时的两层小洋楼，地砖上</p>
          <p>保留的几处花纹、二楼缤纷</p>
          <p>的花窗，既有西关风情，也</p>
          <p>融合了南洋的建筑元素，满</p>
          <p>是爷爷奶奶小时候的记忆。</p>
        </div>
      </div>
    </transition>
    <transition name="part2">
      <div class="part2" v-if="part2Show">
        <div class="indexBox build1Shu part2Index" @mousewheel="mouseWheel2">
          <div class="index" ref="index">
            <div
              class="part2Box"
              :style="{ transform: `translateY(${part2PicTop}px)` }"
              ref="part2Box"
            >
              <div class="part2Box1">
                <div
                  class="part2ImgBox1"
                  :style="{ transform: `translateY(${part2SrollTop1}px)` }"
                >
                  <img
                    src="@/assets/buildInfo/build3/天-12.png"
                    alt=""
                    class="part2Img1"
                  />
                </div>
                <div class="part2Content1">
                  <p class="part2Content1P1">天·大厨 Simon哥</p>
                  <p class="part2Content1P2">有多年五星级酒店的主厨经验，</p>
                  <p class="part2Content1P2">每个细节都务求亲历其为。</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part3">
      <div
        class="part3 part"
        @mousewheel.prevent="mouseWheel3"
        v-if="part3Show"
      >
        我是图片文字穿插
      </div>
    </transition>
  </div>
</template>

<script>
import BackHome from "../../../components/backHome.vue";
import PoPup from "../../../components/popup.vue";

export default {
  name: "build3",
  data() {
    return {
      wordTop: 0,
      imgLeft: 0,
      imgWidth: null,
      wordHeight: null,
      screenY: null,
      screenX: null,
      indexHeight: 0,
      fromPage: "",
      part1Show: true,
      part2Show: false,
      part3Show: false,
      // 下面是part2内部变量
      part2PicTop: 0, //图片盒子的translateY
      part2BoxHeight: 0, //图片盒子元素的1/10高度
      part2SrollY: 0,
      part2SrollTop1: 0,
    };
  },
  components: {
    BackHome,
    PoPup,
  },
  mounted() {
    // this.buildIndex = this.$route.query.index; //this.buildIndex是建筑的序号
    this.wordHeight = this.$refs.word1.offsetHeight;
    this.screenX = document.body.clientWidth;
    this.screenY = document.body.clientHeight;
    // this.indexHeight = this.$refs.index.offsetHeight;
  },
  methods: {
    mouseWheel(e) {
      //deltaY-125往上滚
      //下面页面往上顶
      if (e.deltaY > 0) {
        this.wordTop =
          this.wordTop - 15 < -(this.wordHeight - this.screenY)
            ? -(this.wordHeight - this.screenY)
            : this.wordTop - 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      } else {
        //往上滚
        this.wordTop = this.wordTop + 15 > 0 ? 0 : this.wordTop + 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      }
      if (this.wordTop <= -(this.wordHeight - this.screenY)) {
        this.part2Show = true;
        setTimeout(() => {
          this.part2BoxHeight = (1 / 10) * this.$refs.part2Box.offsetHeight; //2/3元素的高度
        }, 0);
      } else {
        // 第二个页面消失
        this.part2Show = false;
      }
    },
    imgLoad() {
      this.imgWidth = this.$refs.banner1.offsetWidth;
    },
    mouseWheel2(e) {
      // 下面是part2内部逻辑
      this.part2SrollY += e.wheelDeltaY * 0.15;
      if (this.part2SrollY > 0) {
        this.part2SrollY = 0;
        this.part2PicTop = this.part2SrollY;
      } else if (this.part2SrollY > ~this.part2BoxHeight) {
        this.part2PicTop = this.part2SrollY;
      } else {
        this.part2SrollY = ~this.part2BoxHeight;
        this.part2PicTop = ~this.part2BoxHeight;
      }
      this.part2SrollTop1 = this.part2SrollY * 0.2;

      // 下面是总页面逻辑跳转
      if (this.part2Show) {
        this.indexHeight =
          this.indexHeight == 0
            ? this.$refs.index.offsetHeight
            : this.indexHeight;
        if (this.$refs.index.scrollTop == 0 && e.deltaY < 0) {
          //第二part消失
          this.part2Show = false;
          this.part3Show = false;
        }

        if (
          this.$refs.index.scrollTop >= this.screenY + this.indexHeight - 50 &&
          e.deltaY > 0
        ) {
          //第三part出现
          this.part3Show = true;
        }
      }
    },
    mouseWheel3(e) {
      if (e.deltaY < 0) {
        this.part3Show = false;
      }
    },
    isDetail(is) {
      if (is) {
        //在这里要禁用鼠标不能滚动--背景板不能滚动
      } else {
        // 恢复鼠标滚轮事件
      }
    },
  },
};
</script>
<style scoped>
.part {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  transition: 0.7s linear;
  background: blue;
}
.part1 {
  position: relative;
  background-color: rgb(225, 226, 209);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 0.7s linear;
}
.part2 {
  transition: 0.5s linear;
  position: absolute;
  top: 0;
}
.part3 {
  position: absolute;
  top: 0;
  background-color: rgb(229, 207, 188);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 1s linear;
}
.part1-enter-active,
.part2-enter-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part1-leave-active,
.part2-leave-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-leave-active {
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-enter-active {
  transition: cubic-bezier(0.01, 0.9, 0.1, 0.1);
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.banner {
  height: 100vh;
  padding: 0 10%;
  position: absolute;
  top: 0;
  transition: 1s;
}
.banner img {
  height: 100vh;
}
.word {
  transition: 1s;
  position: absolute;
  left: 0;
  font-size: 110px;
  width: 100vw;
  padding-top: 50px;
  padding-bottom: 35vh;
}
.word p {
  font-family: QingKai;
  text-align: center;
  margin-top: 30px;
  border: 0;
}
.pFirst {
  font-size: 150px;
}
.build1Shu {
  position: absolute;
  transition: 2s;
}

.indexBox {
  height: 100vh;
}
/* 下面是part2的内容 */
.part2Index {
  background-color: #dae6f1;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.part2Box {
  width: 100%;
  height: 110vh;
  transition: 1s;
}
.part2Box1 {
  width: 100%;
  height: 100vh;
  position: relative;
}

.part2ImgBox1 {
  width: 50vw;
  height: 110vh;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 0;
  transition: 1s;
  display: flex;
  align-items: center;
}
.part2Img1 {
  position: absolute;
  width: 85%;
}
.part2Content1 {
  font-family: XiuKai;
  transition: 1s;
}
.part2Content1 {
  position: absolute;
  bottom: 15vh;
  left: 10vw;
  text-align: left;
  z-index: 10;
}
.part2Content1P1 {
  font-size: 5rem;
  font-weight: 600;
  font-family: XiuKai;
}
.part2Content1P2 {
  font-size: 2.6rem;
  font-weight: 600;
  font-family: XiuKai;
}
</style>
